.departments__links::-webkit-scrollbar {
    width: 8px;
}

.departments__links::-webkit-scrollbar-track {
    box-shadow: inset 0 0 4px grey;
    border-radius: 4px;
}

.departments__links::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 4px;
}
